.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

ul.nav-links {
  overflow: hidden;
  float: right;
  margin: 0;
  padding: 10px 0 0;
}

body, html {
  margin: 0 !important;
  padding: 0 !important;
}

div#root {
  display: block;
  margin: 0;
  padding: 0;
  background: #727272;
  height: 100vh;
}

ul.nav-links > li > a {
  font-size: 16px;
  align-items: baseline;
  color: #330707;
  text-decoration: none;
  padding: 5px 20px;
  font-family: Calibri, sans-serif;
}

.s-header {
  background: #586470;
  height: auto;
  margin: 0!important;
  display: list-item;
  overflow: hidden;
  width: auto;
}

ul.nav-links > li {
  display: inline-block;
  justify-items: center;
  margin: 5px 20px;
  height: 40px;
  overflow: hidden;
}